    private clearDrawFeatures(){
        if (this.mapService.vectorSource){
            this.mapService.vectorSource.clear();
        }        
    }    
    
    
    public addTrack(){
        
        
        if (this.enableAddPlacemark){
            this.addPlacemark();
        }
        
        this.enableAddTrack = !this.enableAddTrack;
        
        
        if (this.enableAddTrack){   
            
            this.windyOpacity = 0;
            this.mapService.windyVisibility = 0;
                
            this.trackInteraction = new Draw({
              source: this.mapService.vectorSource,
              type: "LineString"
            });
            this.map.addInteraction(this.trackInteraction);  
            
            
            this.trackInteraction.on('drawend', (event) => {
            
                var feat = event.feature;
                var geometry = feat.getGeometry();
                var coords = geometry.getCoordinates();
                
                let length = this.mapService.getDistance(geometry.getLength());
                
                console.log(length);
                
                
                
                this.alertController.create({
                    header: "Distance",
                    message: length,
                    buttons: [
                        {
                            text: 'Dismiss',
                            role: 'cancel'
                        }
                    ]
                }).then((el) => {
                    el.present(); 
                    
                    el.onDidDismiss().then(() => {           
                        this.map.removeInteraction(this.trackInteraction); 
                        this.mapService.vectorSource.removeFeature(feat);
                        
                    });
                    
                    
                    
                    
                });     
                
                this.enableAddTrack = !this.enableAddTrack;
                
                
          
            });            
            
                       
        }
        else{
            this.map.removeInteraction(this.trackInteraction);   
        }        
    }
    
    
    public addPlacemark(){
        
        if (this.enableAddTrack){
            this.addTrack();
        }
        
        this.enableAddPlacemark = !this.enableAddPlacemark;
        
        
        
        if (this.enableAddPlacemark){    
            
            this.windyOpacity = 0;
            this.mapService.windyVisibility = 0;
               
            this.placemarkInteraction = new Draw({
              source: this.mapService.vectorSource,
              type: "Point"
            });
            this.map.addInteraction(this.placemarkInteraction); 
            
            this.placemarkInteraction.on('drawend', (event) => {
            
                var feat = event.feature;
                var geometry = feat.getGeometry();
                var coords = geometry.getCoordinates();
             
                
                this.showAddPlacemark(coords, feat);
          
            });
            
            
                        
        }
        else{
            
            
            
            this.map.removeInteraction(this.placemarkInteraction);   
        }        
    }    
    
    
    private async showAddPlacemark(coords, feat){


        const modal = await this.modalController.create({
          component: CreatePlacemarkPage,
          componentProps: {placemark:this.user.id}
        });

        modal.onDidDismiss()
            .then((data:any) => {
                if (data && data.data){  
                    console.log(data);   
                          let placemark = data.data.placemark;
                          placemark.latitude = coords[0];
                          placemark.longitude = coords[1];
                          placemark.point_type = "placemark";
                          
                          this.layerService.createPlacemark(placemark).then((data:any) => {
                              feat.setId("placemark-" + data.id);
                              this.getLayers();
                              this.getGroupLayers();  

                              
                              this.mapService.showPlacemark(data);
                              
                                                          
                          });
                          
                          
                          this.map.removeInteraction(this.placemarkInteraction); 
                          this.enableAddPlacemark = !this.enableAddPlacemark;
                          
                          
                }  
                else{
                    console.log("remove feat");
                    this.mapService.vectorSource.removeFeature(feat);



                    this.map.removeInteraction(this.placemarkInteraction); 
                    this.enableAddPlacemark = !this.enableAddPlacemark;                    
                    
                    
                }                      
                    
                   
            });        



        await modal.present();            
    }
    
    
    public recenter(){
        this.recenterActive = false;
        
        if (this.currentPosition.latitude && this.currentPosition.longitude){
            this.mapService.moveMapsLatLng(this.currentPosition.latitude, this.currentPosition.longitude);
        }
                        
        this.geolocation.getCurrentPosition({ enableHighAccuracy : true}).then((resp) => {

            
            if (resp && resp.coords){
                
                this.currentPosition = {latitude: resp.coords.latitude, longitude: resp.coords.longitude}
                
                
                this.mapService.updateCurrentPosition({latitude: resp.coords.latitude, longitude: resp.coords.longitude}); 
                
                if (!this.mapService.leafletMap){
                    
                    this.mapService.moveMapsNoAnimation(resp.coords.latitude, resp.coords.longitude);
                    
                    
                    setTimeout(() => {
                        this.mapService.moveMapsLatLng(resp.coords.latitude, resp.coords.longitude);
                    }, 1000);
                                        
                    
                    
                }
                else{
                    this.mapService.moveMapsLatLng(resp.coords.latitude, resp.coords.longitude);
                }
                
            }

        }).catch((error) => {
            console.log('Error getting location', error);
        });           
    }
    
    
    public watchLocation(){
        let watch = this.geolocation.watchPosition({ enableHighAccuracy : true});
        this.watchSubscription = watch.subscribe((data:any) => {
            
            if (data && data.coords){
                
                let watchDate = new Date();
                if (!this.currentPositionTracked || ((watchDate.getTime() - this.currentPositionTracked.getTime()) > 2000)){
                    //update the users current position
                    this.mapService.updateCurrentPosition({latitude:data.coords.latitude, longitude:data.coords.longitude});
                    this.mapService.moveMapsLatLng(data.coords.latitude, data.coords.longitude);
                    this.currentPositionTracked = watchDate
                }
                
                if (!this.trackedPositionTracked || ((watchDate.getTime() - this.trackedPositionTracked.getTime()) > 10000)){
                    //track the users position
                    let convertedCoords = this.mapService.convertLatLng({latitude:data.coords.latitude, longitude:data.coords.longitude})
                    this.trackedCoordinates.push({latitude:convertedCoords[0], longitude:convertedCoords[1]});
                    this.mapService.addTrackedPosition({latitude:data.coords.latitude, longitude:data.coords.longitude});  
                    this.trackedPositionTracked = watchDate;
                }

                
            }
            
        });        
    }
    
    
    public stopWatchLocation(){
        if (this.watchSubscription){
            this.watchSubscription.unsubscribe();
        }

        
        
    }
    
    
    
